.event {
  font-family: Helvetica, Arial, sans-serif;
  padding: 0;
  display: flex;
  align-items: center;
  width: 100%;

  .card-second-col {
    margin-left: 3px;
    text-align: left;
    padding: 2px 0 2px 5px;
    font-size: 14px;
    @media screen and (min-width: 768px) {
      font-size: 36px;
    }

    .time-row {
      padding-bottom: 3px;
    }

    .break-div {
      color: #BBBBBB;
      margin: 3px 0 3px 0;
      width: 100%;
    }
  }
}
