.chooser {

  .top {
    margin: 20px auto 0 auto;
    width: 60%;

    .arrow {
      text-align: center;
      font-size: 25px;
      background-color: white;
      color: gray;
    }

    .month-year-container {
      text-align: center;
      flex: 1;

      .datetime-picker-container {
        width: 100%;
        display: block;

        .datetime-picker {
          display:inline-block;
          vertical-align: middle;
        }
      }
    }

  }

}
