.daily-note {
  font-family: Helvetica, Arial, sans-serif;
  padding: 0;
  display: flex;
  align-items: center;
  width: 100%;
  background-color: white;

  .card-second-col {
    margin-left: 3px;
    border-left: 6px solid black;
    text-align: left;
    padding: 2px 0 2px 5px;
    font-weight: 500;
    font-size: 14px;
    @media screen and (min-width: 768px) {
      font-size: 36px;
    }

    .dept-row {
      font-size: 16px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding-bottom: 3px;
    }

    .note-text-row-oneline {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .note-text-row {
      overflow: inherit;
    }

  }
}
