.clock-controls {
  margin: 0 5px 5px 5px;
  width:100%;

  .switch-button-container {
    .switch-button {
      font-size: 20px;
      font-weight: 1;
    }
  }

  .clockin-button-container {
    .clockin-button {
      font-size: 20px;
      font-weight: 1;
      width: 100%;
      --background: green;
      --border-radius: 5px;
    }
  }

  .clockout-button-container {
    display: table;
    width: 100%;

    .clockout-button {
      display: table-cell;
      font-size: 20px;
      padding-right: 3px;
      font-weight: 1;
      --background: #931C21;
      --border-radius: 5px;
    }

    .break-button {
      display: table-cell;
      font-size: 20px;
      padding-left: 3px;

      font-weight: 1;
      --background: #DAA520;
      --border-radius: 5px;
    }
  }
}

.clockout-modal .modal-wrapper {
  top: 35%;
  width: 50%;
  height: 33%;
  position: absolute;
  display: block;
}

.break-selector-modal .modal-wrapper {
  top: 20%;
  width: 65%;
  height: 50%;
  position: absolute;
  display: block;

  .cancel-button {
    height: 50px;
  }
}
