.chooser {
  .top {
    margin-top: 8px;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    width: 100%;

    .arrow {
      text-align: center;
      font-size: 25px;
      background-color: white;
      color: gray;
    }

    .month-year-container {
      text-align: center;
      flex: 1;

      .datetime-picker-container {
        width: 100%;
        display: block;

        .datetime-picker {
          display:inline-block;
          vertical-align: middle;
        }
      }


    }

  }

  .weekday-list {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    width: 100%;
    height: 50px;
    font-size: 14px;

    .weekday-button {
      flex: 1;
      text-align: center;
      padding: 0 10px 0 5px;

      .today {
        border-bottom: 2px solid black;
      }

      .not-today {
        border-bottom: 2px solid white;
      }
    }
  }
}
