.label {
  font-size: 16px;
}

#clock-page {
  height: 100%;

  ion-list-header {
    color: #BBBBBB;
    font-weight: 500;
  }

  .scroll {
    background: #ededed;
  }

  .outer-content {
    .grid {
      .current-event-row {

        .current-event-col {
          display: inherit;

        }
      }
    }
  }

  .todaylist {
    margin-bottom: auto;
    height: 100%;
  }

  .center {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .footer {
    position: absolute;
    bottom: 0;
    padding-bottom: 0;
  }
}
