.daily-note {
  margin: 0 0 10px 5px;
  font-family: Helvetica, Arial, sans-serif;
  padding: 0;
  display: flex;
  align-items: center;
  width: 100%;
  background-color: white;

  .card-first-col {
    min-width: 30px;
    font-size: 13px;
    padding: 0;
    font-weight: 600;
    display: flex;
    align-items: stretch;
    flex-direction: column;

    .day-name {
      padding: 3px 0 3px 0;
    }

    .day-number {
      padding: 3px 0 3px 0;
    }
  }

  .card-second-col {
    font-weight: 500;
    font-size: 16px;
    border-left: 6px solid black;

    .first-row {
      font-weight: 550;
      height: 26px;
      line-height: 18px;
    }
  }
}
