.event {
  margin: 0 0 10px 5px;
  font-family: Helvetica, Arial, sans-serif;
  padding: 0;
  display: flex;
  align-items: center;
  width: 100%;
  background-color: white;

  .card-first-col {
    min-width: 30px;
    font-size: 13px;
    padding: 0;
    display: flex;
    align-items: stretch;
    flex-direction: column;

    .day-name {
      padding: 3px 0 3px 0;
    }

    .day-number {
      padding: 3px 0 3px 0;
    }
  }

  .card-second-col {
    font-size: 16px;

    .time-row {
      height: 18px;
      line-height: 18px;
    }

    .name-row {
      font-size: 14px;
    }

    .position-row {
      color: gray;
      font-size: 14px;
      height: 18px;
      line-height: 18px;
    }


  }
}
